import card from '@/components/card'
import {
	api
} from '../../const'
import {
	passTime
} from '../../utils/index'
import {
	checkT
} from '../../utils/net'
import login from '../../components/login'

export default {
	components: {
		login,
		card
	},
	data() {
		return {
			t: 0,
			noticeList: [],
			loginVisible: false
		}
	},
	computed: {
		formatNoticeList: function() {
			return this.noticeList.map(_ => {
				return {
					..._,
					createtime: passTime(_.createtime)
				}
			})
		}
	},
	onShow() {
		var that = this;
		var t = uni.getStorageSync("t")
		checkT(t,
			function() {
				that.loginVisible = true
			},
			function() {
				that.t = uni.getStorageSync('t')
				that.getData()
				that.readAllNotices()
			}
		);

	},
	methods: {
		async readAllNotices() {
			this.$http({
				t: this.t,
				act: 'notice.readAll'
			},function(res){
				uni.removeTabBarBadge({
					index: 2
				})
			})
		},
		closeModalEvent() {
			console.log("fd");
			this.loginVisible = false
			this.t = uni.getStorageSync("t")
		},
		goPostDetail(e) {
			const postId = e.currentTarget.dataset.postid
			uni.navigateTo({
				url: `../detail/main?postId=` + postId
			})
		},
		goDetail(e) {
			uni.navigateTo({
				url: `../detail/main?topicid=${e.target.dataset.topicid}`
			})
		},
		async getData() {
			var that = this
			this.$http({
				act: 'notice.list',
				t: this.t,
				pageNum: 0,
				size: 10
			},function(res){
				that.noticeList = res.data.list
			})
		}
	}
}
